body {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    margin: 0;
    padding: 0;
    background-color: #e0e0e0;
    color: #333;
  }
  
  .header {
    text-align: center;
    padding: 20px;
    background-color: #3f51b5;
    color: white;
    margin-bottom: 20px;
  }
  
  .header h1 {
    margin: 0;
    font-size: 2.5em;
  }
  
  .header h2 {
    margin: 0;
    font-size: 1.5em;
  }
  
  .header p {
    margin: 5px 0 0;
  }
  
  .section {
    padding: 20px;
    border-bottom: 1px solid #ddd;
    background-color: #f5f5f5;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  .section h3 {
    font-size: 1.8em;
    color: #3f51b5;
    margin-bottom: 10px;
  }

  .section h4 {
    font-size: 1.2em; /* Ajusta el tamaño según tus necesidades */
    margin-bottom: 5px;
    color: #3f51b5;
  }
  
  
  .section p {
    margin: 0;
  }
  
  .job h4 {
    font-size: 1.4em;
    margin-bottom: 5px;
  }
  
  .job p {
    margin: 0;
    font-size: 1.2em;
    color: #666;
  }
  
  ul {
    list-style-type: disc;
    margin: 10px 0 0 20px;
    padding: 0;
  }
  
  ul li {
    margin-bottom: 5px;
  }


a {
  color: #ffeb3b; /* Cambié el color para que resalte más */
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.custom-divider {
  margin: 16px 0; /* Ajusta el margen según tus necesidades */
  padding: 16px 0;
}
  